body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
  display: block;
  margin: auto;
  margin-top: 50px;
  width: 60%;
}

.form {
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.input {
  margin-left: 10px;
  border: 1px solid red;
}

.title {
  text-align: center;
}

.fixed-height {
  border: 1px solid red;
  width: 60%;
  margin: auto;
  height: 75px;
  text-align: center;
  display: block;
  overflow-wrap: break-word;
  height: 50px;
}

.auto-height {
  border: 1px solid red;
  width: 60%;
  margin: auto;
  height: 75px;
  text-align: center;
  display: block;
  overflow-wrap: break-word;
  height: auto;
}
